
@font-face {
  font-family: "Varela";
  src: url(./fonts/VarelaRound-Regular.ttf) format("truetype");
}

body {
    font: 14px "Century Gothic", Futura, sans-serif;
    height: 100vh;
    width: 100vw;
    color: rgb(255, 255, 255);
    background-image: linear-gradient(to top right, #000428 15%, #04559c 100%);
    overflow: hidden; 
  }



  .vl {
    border-left: 6px solid ivory;
    height: auto;
    position: absolute;
    left: 50%;
    margin-left: -27px;
    top: 200px;
    bottom: 110px;
    z-index: 3;
  }

   

  #Title {position:absolute;
    display:inline;
    align-self: center;
    font-size: 10vw;
    color: ivory;
    margin-top: -3%;
    margin-bottom: 90%;
    margin-left: 3.4rem;
    margin-right: auto;
    text-shadow: 0rem 0rem .9rem  rgb(6, 6, 34);
  }

  
  ol, ul {
    padding-left: 30px;
    list-style-type: none;
  }

  .board {
    position: relative;
    margin-left: -20%;
    width: max-content;
    height: max-content;
    border-radius: 6px;
    box-shadow: 0rem 0rem 0.8rem .5rem  rgba(0, 0, 0, 0.25);
  }

  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  #status {
    font-weight: bold;
    font-size: 43px;
    margin-top: -5px;
    margin-bottom: 5px;
  }
  
  #square-0 {
    border-top-left-radius: 6px;
  }
  
  #square-2 {
    border-top-right-radius: 6px;
  }
  
  #square-6 {
    border-bottom-left-radius: 6px;
  }
  
  #square-8 {
    border-bottom-right-radius: 6px;
  }
  .square {
    background: rgba(241, 241, 241, 0.555);
    border: 1px solid rgb(107, 107, 107);
    float: left;
    top: 10%;
    left: 50%;
    color: ivory;
    font-size: 69px;
    font-weight: bold;
    line-height:70%;
    height:90px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 10px;
    text-align: center;
    width: 90px;
    
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }

  ::-webkit-scrollbar {
      width: 6px;
  
  }
   
  ::-webkit-scrollbar-track {
      border-radius: 5px;
      background-color: rgba(196, 194, 194, 0.644);
      width: 7px;
  }
   
  ::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: rgb(241, 238, 238);
      width: 5px; 
    }
  
  

  #history-moves {
    width: max-content;
    height: 171px;
   
    border-radius: 5px;
    overflow-x: hidden;
    overflow-y: auto;
    text-align: center;
    padding: 0px;
    margin-top: 20px;
    
  }

  .button-history {
    background: rgba(241, 241, 241, 0.555);
    border: 2px solid rgb(240, 240, 240);
    border-radius: 5px;
    margin: 2px;
    color: ivory;
    font-family: Varela;
    font-size: 20px;
    width: 210px;
    height: 40px;
  }

  .button-history:hover {
    background:  rgb(240, 240, 240);
    color: rgba(34, 33, 33, 0.555);
  }
  
  .game-board{
    align-self: center;
    padding-left: 20%;
    padding-top: 10%;
    padding-right: 20%;
    margin-top: 26%;
    margin-bottom: 20%;
    width: 50%;
    padding-bottom: 10%;
    background-color: rgba(175, 58, 84, 0.582);
    border-radius: 8px;
    box-shadow: 0rem 0rem 0.8rem .5rem  rgba(0, 0, 0, 0.25);
  }

  .game {
    position:absolute;
    left: 5%;
    right: 5%;
    top: 5rem;
    bottom: 5rem;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    position:absolute;
    display: inline-flexbox;
    margin-left: 60%;
    margin-right: 10%;
    margin-top: 12rem;
    margin-bottom: 10rem;
    
  }
  
  @media only screen and (max-width: 1026px) {
    .vl {
        display:none;
    }
   
    #Title {
      margin-left: 2%;
      margin-top: 25%;
      font-size: 16.8vw;
    }
    
    .game{
      width:90%;
    }
    .game-board {
    padding-left: 21vw;
    padding-right: 21vw;
    margin-left: auto;
    margin-right: auto;
    }
    
    #status {
      font-size: 9vw;

    }

    #history-moves {
      margin-top: 7px;
    }

    .game-info {
      margin-top: 63vh;
      margin-left: 15%;
      margin-right: 13% ;
    }
    
}